import React, { useState } from "react";
import {
  Col,
  Row,
  Button,
  Collapse,
  Card,
  Form,
  Tab,
  Nav,
  Container,
  InputGroup,
  DropdownButton,
  Dropdown,
} from "react-bootstrap";

import { FaSearch, FaSyncAlt, FaPaste, FaTachometerAlt } from "react-icons/fa";
import { IconContext } from "react-icons";
import styles from "./DashBoard.module.css";

import Search from "student/components/Search/Search";
import AdminOrderTable from "student/components/Table/AdminOrderTable/AdminOrderTabla";

import {
  Link,
  Switch,
  Route,
  NavLink,
  withRouter,
} from "react-router-dom";
import { Redirect } from "react-router-dom";
;

const dRouters = [
  {
    name: "New Order",
    path: "/new",
    icon: <FaPaste />,
    Component: AdminOrderTable,
    status: "new",
  },
  {
    name: "Awaiting Order",
    path: "/await-order",
    icon: <FaTachometerAlt />,
    Component: AdminOrderTable,
    status: "await",
  },
  {
    name: "Proccess Order",
    path: "/process-order",
    icon: <FaTachometerAlt />,
    Component: AdminOrderTable,
    status: "process",
  },
  {
    name: "Complete Order",
    path: "/complete-order",
    icon: <FaTachometerAlt />,
    Component: AdminOrderTable,
    status: "complete",
  },
  {
    name: "Complete Deliverd Order",
    path: "/complete-deliverd-order",
    icon: <FaTachometerAlt />,
    Component: AdminOrderTable,
    status: "new",
  },
];

const DashBoard = ({ location, match }) => {
  const [ awaitValue, setAwaitValue ] = useState('user');

  const linkClickHandler = (state) =>{
    setStatus(state);
  }
  const dropdownHandler = (v) => {
    setAwaitValue(v);
  }

  return (
    <div className={`${styles.bgLightWhite} position-relative`}>
      <Tab.Container id="left-tabs-dashboard-example">
        <div className={`${styles.darkBlue} rounded-top p-2 `}>
          <Search />
          <Row className="d-md-block d-none">
            <Container>
              <InputGroup className="mb-1 mt-2">
                <DropdownButton
                  onChange={(e) => console.log("console", e)}
                  variant="outline-secondary"
                  title="Await"
                  id="input-group-dropdown-1"
                >
                  <Dropdown.Item onClick={() => dropdownHandler("")}>
                    none
                  </Dropdown.Item>
                  <Dropdown.Divider />
                  <Dropdown.Item onClick={() => dropdownHandler("admin")}>
                    Adimn
                  </Dropdown.Item>
                  <Dropdown.Divider />
                  <Dropdown.Item onClick={() => dropdownHandler("user")}>
                    User
                  </Dropdown.Item>
                </DropdownButton>
                {/* <FormControl aria-label="Text input with dropdown button" value='thid'  /> */}
                <Form.Control
                  style={{ color: "transparent", textShadow: "0 0 0 #000000" }}
                  placeholder="Select Awaiting"
                  readOnly
                  value={awaitValue}
                />
              </InputGroup>
              <IconContext.Provider value={{ size: 30 }}>
                <Nav
                  variant="pills"
                  className={`${styles.tabNav} d-flex mb-3 `}
                >
                  {dRouters.map((e, i) => (
                    <Nav.Item key={i}>
                      <Nav.Link
                        as={NavLink}
                        to={`${match.path}${e.path}`}
                        className={`${styles.tabBtn}`}
                        activeClassName={styles.tabBtnActive}
                      >
                        {e.icon}
                        {e.name}
                      </Nav.Link>
                    </Nav.Item>
                  ))}
                </Nav>
              </IconContext.Provider>
            </Container>
          </Row>
        </div>
        <div className="tab-content mt-4">
          <Tab.Content>
            <Switch>
              {dRouters.map((E, i) => {
                return (
                  <Route key={i} path={`${match.path}${E.path}`}>
                    <E.Component  status={E.status} awaitValue={awaitValue} />
                  </Route>
                );
              })}
              <Redirect to={`${match.path}/new`} />
            </Switch>
          </Tab.Content>
        </div>
      </Tab.Container>
    </div>
  );
};

export default withRouter(DashBoard);
